import $ from 'jquery'

    let post = async() => {
        let p = await new Promise((reslove, reject)=>{
            setTimeout(()=>{
                reslove('POST')
            },1000)
        })
        return p
    }
    let get = () => {
        let ret
        $.get('http://localhost:3000/test',(data)=>{
            ret = data
        })
        return ret
    }

export {post, get}



